"use client";

import { useEffect, useRef } from "react";
import mapboxgl from "mapbox-gl";
import zoningData from "./data/zoning.json"; // ðŸ”¹ sample zoning data

export default function MapPage() {
  const mapContainer = useRef(null);

  useEffect(() => {
    mapboxgl.accessToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN;

    const map = new mapboxgl.Map({
      container: mapContainer.current,
      style: "mapbox://styles/mapbox/streets-v12",
      center: [-111.93, 33.42],
      zoom: 12,
    });

    // Navigation & fullscreen controls
    map.addControl(new mapboxgl.NavigationControl(), "top-right");
    map.addControl(new mapboxgl.FullscreenControl(), "top-right");

    map.on("load", () => {
      // 1ï¸âƒ£ Add zoning polygons as a GeoJSON source
      map.addSource("zoning", {
        type: "geojson",
        data: zoningData,
      });

      // 2ï¸âƒ£ Fill layer (colors for each zoning type)
      map.addLayer({
        id: "zoning-fill",
        type: "fill",
        source: "zoning",
        paint: {
          "fill-color": [
            "match",
            ["get", "zone"],
            "R1 - Single Family", "#1f77b4", // blue
            "R3 - Multifamily", "#ff7f0e",   // orange
            "#ccc" // fallback grey
          ],
          "fill-opacity": 0.4,
        },
      });

      // 3ï¸âƒ£ Outline for polygons
      map.addLayer({
        id: "zoning-outline",
        type: "line",
        source: "zoning",
        paint: {
          "line-color": "#000",
          "line-width": 2,
        },
      });

      // 4ï¸âƒ£ Popup on click
      map.on("click", "zoning-fill", (e) => {
        const zone = e.features[0].properties.zone;

        new mapboxgl.Popup()
          .setLngLat(e.lngLat)
          .setHTML(`<strong>Zoning:</strong> ${zone}`)
          .addTo(map);
      });

      // Change cursor on hover
      map.on("mouseenter", "zoning-fill", () => {
        map.getCanvas().style.cursor = "pointer";
      });
      map.on("mouseleave", "zoning-fill", () => {
        map.getCanvas().style.cursor = "";
      });
    });

    return () => map.remove();
  }, []);

  return (
    <div style={{ height: "100vh", width: "100vw", position: "relative" }}>
      {/* Title bar */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          backgroundColor: "rgba(0,0,0,0.6)",
          color: "white",
          textAlign: "center",
          padding: "8px",
          zIndex: 1,
        }}
      >
        Zonix | Tempe Zoning Map
      </div>

      {/* Map container */}
      <div ref={mapContainer} style={{ height: "100%", width: "100%" }} />
    </div>
  );
}
